<script>
export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data () {
    return {
      title: 'Create a new Todo...',
      text: null
    }
  },
  methods: {
    handleSubmit () {
      this.axios.post('http://192.169.178.237:8055/masters/signal/', { messages: this.text }).then((result) => {
