<template>
 <header class="card">
    <h1>Notify</h1>
    <button id="theme-switcher" @click="visible()" >
        <img src="../assets/images/bell.png" alt="Change color theme" class="themeswitch" />
        <span class="count">{{ count }}</span>
    </button>
  </header>
  <SideBar v-if="isVisible" />
</template>
<script>
import SideBar from '@/components/SideBar.vue'
import axios from 'axios'
export default {
  name: 'HeaderView',
  components: {
    SideBar
  },
  data: function () {
    return {
      count: 0,
      isVisible: false
    }
  },
  methods: {
    visible () {
      this.isVisible = !this.isVisible
    }
  },
  mounted () {
    axios.get('http://192.169.178.237:8055/masters/signal/1').then((result) => {
      this.count = result.data.cnt
    })
  }
}
</script>
