<template>
    <aside class="cart-container" v-if="isVisi">
        <div class="cart">
            <h1 class="cart-title spread">
            {{ notify }}
            <span style="float:right; padding-right:10px;" class="cart-close" @click="visible()">&times;</span>
            </h1>

            <div class="cart-body">
            <table class="cart-table">
                <thead>
                <tr>
                    <th><span class="sr-only">S.No</span></th>
                    <th><span class="sr-only">Messages</span></th>
                    <th><span class="sr-only">Create Date</span></th>
                </tr>
                </thead>
                <tbody>
                <tr v-for="item in list" v-bind:key="item.id">
                    <td>{{ item.id }}</td>
                    <td>{{ item.messages }}</td>
                    <td>{{ item.createdt }}</td>
                </tr>
                </tbody>
            </table>
            </div>
        </div>
    </aside>
</template>

<script>
import axios from 'axios'
export default {
  name: 'SideBar',
  data: function () {
    return { list: undefined, notify: 'Notify', isVisi: true }
  },
  methods: {
    visible () {
      this.isVisi = !this.isVisi
    }
  },
  mounted () {
    axios.get('http://192.169.178.237:8055/masters/signal?sw=a').then((result) => {
      this.list = result.data
    })
  }
}
</script>
